<template>
  <el-date-picker
    v-model="dateRange"
    type="datetimerange"
    :picker-options="pickerOptions"
    value-format="yyyy-MM-dd HH:mm:ss"
    range-separator="-"
    :start-placeholder="$t('common.beginTime')"
    :end-placeholder="$t('common.endTime')"
    @change="handleDateChange"
  />
</template>

<script>
export default {
  name: 'CtDatePicker',
  props: {
    //  begin  end
    begin: {
      type: String,
      default: '',
    },
    end: {
      type: String,
      default: '',
    },
    oneMonthRange: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      // 
      dateRange: [this.begin, this.end],
      choiceDate0: '',
      pickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          if (minDate) {
            this.choiceDate0 = minDate.getTime()
          } else {
            this.choiceDate0 = new Date()
          }
          if (maxDate) {
            this.choiceDate0 = ''
          }
        },
        disabledDate: (time) => {
          if (!this.oneMonthRange) {
            return
          }
          const choiceDateTime = new Date(this.choiceDate0).getTime()
          if (this.choiceDate0) {
            const diff = 30 * 24 * 3600 * 1000
            return (
              time.getTime() > choiceDateTime + diff ||
              time.getTime() < choiceDateTime - diff
            )
          }
          return false
        },
      },
    }
  },
  watch: {
    //  begin  end  dateRange
    begin(newbegin) {
      !newbegin && (this.dateRange = ['', '']) //  begin  end
    },
    end(newend) {
      !newend && (this.dateRange = ['', '']) //  end  begin
    },
    //  dateRange 
    dateRange(newRange) {
      let rangge = newRange
      if (!newRange) {
        rangge = ['', '']
      }
      this.$emit('update:begin', rangge[0]) //  begin
      this.$emit('update:end', rangge[1]) //  end
    },
  },
  methods: {
    handleDateChange() {
      // 
      console.log('', this.dateRange)
    },
  },
}
</script>
