<template>
  <ct-select
    v-model="billCycle"
    :placeholder="selectPh('dataUsage.billCycle')"
    :options="billCycleOptions"
    :clearable="false"
    v-bind="$attrs"
  >
  </ct-select>
</template>

<script>
import langMixin from '@/mixins/langMixin'
import CtSelect from '../CtSelect'
export default {
  name: 'BillCycleSelect',
  components: {
    CtSelect,
  },
  mixins: [langMixin],
  props: {
    value: {
      type: [String, Number],
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      billCycle: '',
    }
  },
  computed: {
    billCycleOptions() {
      const list = []
      // 
      const currentDate = new Date()
      // 
      for (let i = 0; i < 7; i++) {
        // 
        const pastDate = new Date(
          currentDate.getFullYear(),
          currentDate.getMonth() - i,
          1
        )
        // getMonth() 0
        const year = pastDate.getFullYear()
        const month = ('0' + (pastDate.getMonth() + 1)).slice(-2) // 

        //  "202306"
        const value = `${year}${month}`
        const label = `${year}-${month}`
        // 
        list.push({
          label,
          value,
        })
      }
      // 
      return list
    },
  },
  watch: {
    value: {
      handler(newVal, oldVal) {
        if (newVal !== oldVal) {
          this.billCycle = newVal
        }
      },
      immediate: true,
    },
    billCycle(val) {
      this.$emit('input', val)
    },
  },
  mounted() {
    this.billCycle = this.billCycleOptions[0].value || ''
  },
  methods: {},
}
</script>

