import Vue from 'vue'
import VueI18n from 'vue-i18n'
import locale from 'element-ui/lib/locale'
import enLocale from 'element-ui/lib/locale/lang/en' // element ui
import zhLocale from 'element-ui/lib/locale/lang/zh-CN' // element ui
import zh from './zh' // zh.js
import en from './en' // en.js
Vue.use(VueI18n)

const messages = {
  en: {
    message: 'hello',
    ...enLocale,
    ...en, // 
  },
  zh: {
    message: '',
    ...zhLocale,
    ...zh, // 
  },
}

// Create VueI18n instance with options
const i18n = new VueI18n({
  locale: localStorage.getItem('language') || 'zh', // set locale   //local value enzh
  messages, // set locale messages
  silentTranslationWarn: true,
})
locale.i18n((key, value) => i18n.t(key, value))
export default i18n // i18n
