export function parseQueryString(url) {
  const queryString = url.split('?')[1] // 
  const params = {} // 

  if (queryString) {
    const pairs = queryString.split('&') // 
    for (const pair of pairs) {
      const [key, value] = pair.split('=') // 
      params[key] = value ? decodeURIComponent(value) : '' // 
    }
  }

  return params
}
