import fetch from '@/request/fetch'

const baseUrl = '/sys/user/smsVerify' // 
export default {
  smsVerify: {
    // 
    sendWebAccountSmsCode() {
      return fetch('sysUser/sendWebAccountSmsCode', {}, { method: 'get' })
    },
    // 
    verifyWebAccountSmsCode(smsCode) {
      return fetch(
        'sysUser/verifyWebAccountSmsCode?smsCode=' + smsCode,
        {},
        { method: 'get' }
      )
    },
    verifyCorpAccountSmsCode(smsCode) {
      return fetch(
        `/sysUser/verifyCorpAccountSmsCode?smsCode=` + smsCode,
        {},
        { method: 'get' }
      )
    },
    smsVerifyVerifySmsCode(smsCode, operCode) {
      return fetch(
        `/sys/user/smsVerify/verifySmsCode?smsCode=${smsCode}&operCode=${operCode}`,
        {},
        { method: 'get' }
      )
    },
    sendSmsCode(params) {
      return fetch(
        `${baseUrl}/sendSmsCode`,
        {
          t: Math.random(),
          operCode: params.operCode,
        },
        { method: 'get' }
      )
    },
    verifySmsCode(params) {
      return fetch(`${baseUrl}/verifySmsCode`, params, { method: 'get' })
    },
    // 
    qryTwiceAuthSwitch() {
      return fetch('/twiceAuth/qryTwiceAuthSwitch', {}, { method: 'get' })
    },
    // 
    sendCheckNum(operName) {
      return fetch(`/twiceAuth/sendCheckNum?operName=${operName}`, {}, { method: 'get' })
    },
    // 
    authCheckNum(checkNum, operName) {
      return fetch(
        `/twiceAuth/authCheckNum?checkNum=${checkNum}&operName=${operName}`,
        {},
        { method: 'get' }
      )
    },
  },
}
