export default {
  methods: {
    inputPh(key) {
      const prefix = this.$i18n.locale === 'zh' ? '' : 'Please Input '
      return prefix + this.$t(key)
    },
    selectPh(key) {
      const prefix = this.$i18n.locale === 'zh' ? '' : 'Please Select '
      return prefix + this.$t(key)
    },
  },
}
