import Vue from 'vue'
export default () => {
  Vue.directive('watermark', (el, binding) => {
    function addWaterMarker(str1, parentNode, font, textColor) {
      // str1
      const img = document.createElement('canvas') // canvas
      parentNode.appendChild(img)
      img.width = 300
      img.height = 150
      img.style.display = 'none'
      const imgs = img.getContext('2d') // canvas
      imgs.rotate((-20 * Math.PI) / 180) // /9
      imgs.font = font || '14px Microsoft JhengHei'
      imgs.fillStyle = textColor || 'rgba(200, 200, 200, 0.3)'
      imgs.textAlign = 'left'
      imgs.textBaseline = 'Middle'
      imgs.fillText(str1, 15, 100) // 
      parentNode.style.backgroundImage = 'url(' + img.toDataURL('image/png') + ')'
    }
    addWaterMarker(binding.value.text, el, binding.value.font, binding.value.textColor)
  })
}
